	module.exports = {"data":{"post":{"html":"<p>Grandma Ruth is in Lüllau to visit Christopher. It's nice not to be tied to the day care schedule, for a couple of days at least.</p>\n<p>I got 2 Elsa AirLancer wireless (802.11b) cards on Monday, so that my Think Pad would be truly wireless. Today, 4 days later, I finally figured out the correct parameters so the card is recognized by my Linux router. I've rarely been so happy about a successful ping!</p>","fields":{"title":"Oma Ruth","date":"22 Jun 2001","url":"https://papascott.de/2001-06-22-oma-ruth/"}}},"pathContext":{"slug":"/2001-06-22-oma-ruth/","prev":{"fields":{"title":"Happy Earlyday","date":"20 Jun 2001","slug":"/2001-06-20-happy-earlyday/"}},"next":{"fields":{"title":"Spargel","date":"23 Jun 2001","slug":"/2001-06-23-spargel/"}}}}

